.pageContainer {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fef9ef;
    font-family: 'Tahoma', sans-serif;
    color: #d35400;
    text-align: center;
}

.title {
    font-size: 3rem;
    margin-bottom: 20px;
    animation: slideIn 1s forwards;
}

.subtitle {
    font-size: 1.2rem;
    margin-bottom: 40px;
}

@keyframes bounce {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-20px);
    }
}
@keyframes slideIn {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }
    to {
        transform: none;
        opacity: 1;
    }
}

.bounceBox {
    width: 50px;
    height: 50px;
    background-color: #d35400;
    border-radius: 10px;
    margin: 0 auto;
    animation: bounce 1s infinite alternate;
}

@media screen and (max-width: 900px) {
    .title{
        font-size: 2rem;
    }
    .subtitle{
        font-size: 1rem;
    }
    .bounceBox{
        width: 30px;
        height: 30px;
        border-radius: 5px;
    }

}